*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.card {
    border: 1px solid #ddd;
    padding: 10px;
    margin: 10px;
    width: 20%;
    border-radius: 20px;
    box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.9);
}

.teste{
    display: flex; 
    justify-content: center;
    width: 100%;
    background-color: white;
    margin-bottom: 25px;

}

.title{
    font-size: 30px;
}
