.pai {
  text-align: center;
  margin-top: 10%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

.app{
  border: 1px solid black;
  width: 70%;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.9);
  border-radius: 20px;
}

.app h1{
  text-align: center;
  background-color: rgba(192, 187, 187, 0.266);
  width: 100%;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.4);
  margin-bottom: 50px;
  padding: 10px;
}

.app  button{
  font-size: 15px;
  padding: 10px;
  border-radius: 10px;
  background-color: rgb(234, 229, 229);

}
